import { createSlice } from '@reduxjs/toolkit';

import { IAccountModel, IStateModel } from 'store/index';


export const objectDetail = createSlice({
  name: 'account',
  initialState: {
    detail : {}
  },
  reducers: {
    
    setObjectDetail: (state: any, action: {type : string, payload : Object}) => {
      
      state.detail = action.payload;
    },
    
  },
});

export const { setObjectDetail } = objectDetail.actions;

export const getDetail = (state: IStateModel) => state.objectDetail.detail;


export default objectDetail.reducer;
