import React from 'react';

import {DEFAULT_PAGE_WIDTH} from 'const';

const style = {
    width : DEFAULT_PAGE_WIDTH,
    margin : '0px auto'
} as React.CSSProperties;

export default function (props: { left: React.ReactNode; center: React.ReactNode; top: React.ReactNode; }) {

    /**New style */
    return (

        <>
            <div style={style}>
                {props.top}
            </div>
            <div style={{ background: "#f0f2f6", height: "calc(100vh - 50px)"}}>
                <div style={{height:'inherit', ...style}}>
                {props.center}
                </div>

            </div>
        </>

        // <div style={{ display: 'flex', height: 'inherit' }}>
        //     <div style={{ flex: '60px' }}>
        //         {props.left}
        //     </div>
        //     <div style={{ flex: 'calc(100vw - 60px)', width: 'calc(100vw - 60px)' }}>
        //         <div>
        //         {props.top}
        //         </div>
        //         <div style={{background: "#f0f2f6", height: "calc(100vh - 50px)"}}>
        //         {props.center}
        //         </div>
        //     </div>
        // </div>

    );

    /**This's old style */
    return (

        <div style={{ display: 'flex', height: 'inherit',overflowY:'hidden' }}>
            <div style={{ flex: '60px' }}>
                {props.left}
            </div>
            <div style={{ flex: 'calc(100vw - 60px)', width: 'calc(100vw - 60px)' }}>
                <div>
                    {props.top}
                </div>
                <div style={{ background: "#f0f2f6", height: "calc(100vh - 50px)" }}>
                    {props.center}
                </div>
            </div>
        </div>

    );
}


