import React, { ReactNode } from "react";

import {v4} from 'uuid';

export interface TwoLayoutPropsModel {
    left: React.ReactNode,
    right: ReactNode,
    minus?: number,
    margin?: number,
    fixed?: 'left' | 'right',
    textAlign?: 'left' | 'right' | 'center'
}

export const Component2ColumnLayout = (props: TwoLayoutPropsModel) => {

    // fixed
    const f = props.minus == undefined ? '50%' : `${props.minus}px`;

    // the rest
    const tr = props.minus == undefined ? '50%' : `calc(100% - ${props.minus + 1 * (props.margin == undefined ? 0 : props.margin)}px)`;

    const tableStyle = {
        overflow: 'hidden',
        margin: props.margin == undefined ? '0px' : `${props.margin}px`
    } as React.CSSProperties;

    const leftStyle = {
        float: 'left',
        textAlign: props.textAlign == undefined ? 'inherit' : props.textAlign,
        width: props.fixed == undefined || props.fixed == 'left' ? f : tr
    } as React.CSSProperties;

    const rightStyle = {
        float: 'right',
        width: props.fixed == undefined || props.fixed == 'left' ? tr : f,
        textAlign: props.textAlign == undefined ? 'inherit' : props.textAlign,
        marginLeft: props.margin == undefined ? '0px' : `${props.margin}px`
    } as React.CSSProperties;

    return (
        <div style={tableStyle} key={v4()}>
            <div style={leftStyle}>
                {props.left}
            </div>
            <div style={rightStyle}>
                {props.right}
            </div>
        </div>
    )
}
