import { createSlice } from '@reduxjs/toolkit';

import {IPopupState,IStateModel} from 'store/index';

const default_id = 0;

export const popupStore = createSlice({
  name: 'popup',
  initialState: {
    lead: {
      updatedDate : undefined,
      insert: false,
      updateId : 0
    },
    deal: {
      insert: false
    },
    call: false,
    email: false,
    notification: false,
    changePassword: false,
    onboarding: false,
    task: {
      insert: false,
      updateId : 0
    },
    callLog:{
      insert: false,
      updateId : 0
    },
    userSetting: {
      insert: false,      
      isOpen: false,      
    }
  },
  reducers: {
    openLeadInsertPopup : state => {
      state.lead.insert = true;
    },
    openCallLogInsertPopup : (state:any,action:{payload:{updateId:number}}) => {
      state.callLog.insert = true;
      state.callLog.updateId = action.payload.updateId;
    },
    openTaskInsertPopup: (state:any,action:{payload:{updateId:number}}) => {
      state.task.insert = true;
      state.task.updateId = action.payload.updateId;
    },
    setPopup: (state: any, action: {payload : {key : string, value : boolean, id? : number}}) => {
      switch (action.payload.key) {
        case 'lead.insert':
          state.lead.updateId = default_id;
          state.lead.insert = action.payload.value;
          break;
        case 'lead.update':
          state.lead.updateId = action.payload.value == false ? default_id : action.payload.id; // default id mean nothing to do here
          state.lead.insert = action.payload.value;
          break;
        case 'deal.insert':
          state.deal.insert = action.payload.value;
          break;
        case 'call':
          state.call = action.payload.value;
          break;
        case 'email':
          state.call = action.payload.value;
          break;
        case 'notification':
          state.notification = action.payload.value;
          break;
        case 'changePassword':
          state.call = action.payload.value;
          break;
        case 'onboarding':
          state.call = action.payload.value;
          break;
        case 'task.insert':
          state.task.updateId = default_id;
          state.task.insert = action.payload.value;
          break;
        case 'call.log.insert':
          state.callLog.updateId = default_id;
          state.callLog.insert = action.payload.value;
          break;
        case 'userSetting':
          state.userSetting.isOpen = action.payload.value;
          state.userSetting.insert = action.payload.id ? true : false;           
          break;
      }
      console.log(state);
    },
    createLead : (state: any) => {
      state.lead.createDate = Date();
    }
  },
});

export const { setPopup, openLeadInsertPopup,openTaskInsertPopup,openCallLogInsertPopup } = popupStore.actions;


export const isOpenLeadInsert = (state : IStateModel) => {
  return {
    status : state.popup.lead.insert,
    id : state.popup.lead.updateId
  }
};

export const isOpenTaskInsert = (state : IStateModel) => {
  return {
    status : state.popup.task.insert,
    id : state.popup.task.updateId
  }
};

export const isOpenCallLogInsert = (state : IStateModel) => {
  return {
    status : state.popup.callLog.insert,
    id : state.popup.callLog.updateId
  }
};

export const isOpenNotification = (state : IStateModel) => {
  return {
    status : state.popup.notification
  };
}

export const isCreatedLead = (state : IStateModel) => {
  return state.popup.lead.createdDate;
}

export const isOpenInsertUserSetting = (state : IStateModel) => {
  return state.popup.userSetting;
}

export default popupStore.reducer;
