import { IChildrenReactNodeProps } from '../models';

import { getIdentity, validTokenAsync } from "../store/modules/accout.store";

import { Route, Redirect, useLocation, useRouteMatch } from "react-router-dom";

import React, { ReactChild, Suspense, useState } from "react";

import { useSelector } from "react-redux";

import LeftMenu from 'components/core/left-menu';

import TopMenu from 'components/core/top-menu';

import Layout from 'layouts/RootLayout';

import router from 'router';

import NoMath from 'modules/no-math';

export default router;

export const RouteWithSubRoutes = function (route: any) {

    try {

        if (route.isPublic) {
            return (
                <>
                    {/* <Route path="*" />
                    {route.routes != undefined && route.routes.map((sub: any, subi: number) => {
                        console.log(sub.path);
                        return (
                            <Route
                                key={subi}
                                strict
                                exact
                                path={sub.path}
                                render={props => (
                                    <sub.component  {...props} routes={sub.routes} />
                                )}
                            />
                        )
                    })}
                    <Route
                        exact
                        path={route.path}
                        render={props => (
                            <route.component {...props} routes={route.routes} />
                        )}
                    /> */}


                        <Layout
                        left={<></>}
                        top={<TopMenu />}
                        center={
                            <>
                                <Route path="*" />
                                <Route
                                    strict
                                    exact
                                    path={route.path}
                                    render={props => (
                                        <route.component {...props} routes={route.routes} />
                                    )}
                                />
                                {route.routes != undefined && route.routes.map((sub: any, subi: number) => {
                                    console.log(sub.path);
                                    return (
                                        <Route
                                            key={subi}
                                            strict
                                            exact
                                            path={sub.path}
                                            render={props => (
                                                <sub.component  {...props} routes={sub.routes} />
                                            )}
                                        />
                                    )
                                })}
                            </>
                        }
                    />

                </>
            );
        } else {

            return (
                <PrivateRoute>
                    <Layout
                        left={<LeftMenu />}
                        top={<TopMenu />}
                        center={
                            <>
                            <Suspense fallback={<div>Loading...</div>}>
                                <Route path="*" />
                                <Route
                                    strict
                                    exact
                                    path={route.path}
                                    render={props => (
                                        <route.component {...props} routes={route.routes} />
                                    )}
                                />
                                {route.routes != undefined && route.routes.map((sub: any, subi: number) => {
                                    console.log(sub.path);
                                    return (
                                        <Route
                                            key={subi}
                                            strict
                                            exact
                                            path={sub.path}
                                            render={props => (
                                                <sub.component  {...props} routes={sub.routes} />
                                            )}
                                        />
                                    )
                                })}
                                </Suspense>
                            </>
                        }
                    />
                </PrivateRoute>
            );
        }
    } catch (ex) {
        console.log(ex);
        return (
            <NoMath error="Have error" />
        )
    }




}

function PrivateRoute({ children, ...rest }: IChildrenReactNodeProps) {

    const identity = useSelector(getIdentity);

    // const isAuthenticated = identity.isAuthorized;
    const isAuthenticated = true;

    return (
        <Route
            {...rest}
            render={({ location }) =>
                isAuthenticated ? (
                    children
                ) : (
                        <>
                            <Redirect
                                to={{
                                    pathname: "/login",
                                    state: { from: location }
                                }}
                            />
                        </>

                    )
            }
        />
    );



}

