import { configureStore } from '@reduxjs/toolkit';
import popupReducer from './modules/popup.store';
import accountReducer from './modules/accout.store';
import objectDetailReducer from './modules/object.store';

export interface IAccountModel {
  token: string,
  name: string,
  id: string,
  fullname : string,
  isAuthorized: boolean,
  permission : string,//{module : string, action : string}[]
};

export interface ILeadDetailModel {
    result:Array<object>
};

export interface IPopupState {
  lead: {
    insert: boolean,
    updateId : number,
    createdDate : any
  },
  deal: {
    insert: boolean
  },
  call: boolean,
  email: boolean,
  notification: boolean,
  changePassword: boolean,
  onboarding: boolean,
  task: {
    insert: boolean,
    updateId : number
  },
  callLog: {
    insert: boolean,
    updateId : number
  },
  userSetting: {
    insert: boolean,
    isOpen : boolean
  },
}

export interface IObjectDetailModel {
  detail : Object
}

export interface IStateModel {
  account: IAccountModel
  popup : IPopupState,
  objectDetail : IObjectDetailModel
}


export default configureStore({
  reducer: {
    popup: popupReducer,
    account: accountReducer,
    objectDetail : objectDetailReducer 
  },
});
