import React, { Children, useState } from 'react';

import { Menu } from 'antd';
import { UserOutlined, DashboardOutlined, ContactsOutlined, HomeOutlined, DollarOutlined, WechatOutlined, SettingOutlined, AreaChartOutlined } from '@ant-design/icons';

import { useHistory } from "react-router-dom";
import SubMenu from 'antd/lib/menu/SubMenu';

import logo from 'assets/logo-NOVAON.png';

const default_font_style = {
    fontSize: '20px',
    color: 'white',
    // margin : '20px 0px'
}

export default function () {

    const routers = [
        {
            // nothing to do here, leave it alone
            path: "",
            name: "BrandName",
            icon: <DashboardOutlined style={default_font_style} />,
            child: []
        },
        {
            path: "/dashboard/overview",
            name: "Tổng quan",
            icon: <DashboardOutlined style={default_font_style} />,
            child: [
                {
                    path: "/dashboard/overview",
                    name: "Tổng quan kinh doanh",
                    icon: <DashboardOutlined style={default_font_style} />
                },
                {
                    path: "/dashboard/activities",
                    name: "Tổng quan hoạt động",
                    icon: <DashboardOutlined style={default_font_style} />
                }
            ]
        },
        {
            path: "/lead",
            name: "Leads",
            icon: <UserOutlined style={default_font_style} />,
            child: []
        },
        {
            path: "/contact",
            name: "Liên hệ",
            icon: <ContactsOutlined style={default_font_style} />,
            child: []
        },
        {
            path: "/corporate",
            name: "Doanh nghiệp",
            icon: <HomeOutlined style={default_font_style} />,
            child: []
        },
        {
            path: "/deal",
            name: "Deals",
            icon: <DollarOutlined style={default_font_style} />,
            child: []
        },
        // {
        //     path: "/account/39cf604e-18c3-4da5-964f-d58a2d0eaaf2",
        //     name: "Tương tác",
        //     icon: <WechatOutlined style={default_font_style} />,
        //     child: []
        // },
        {
            path: "/report",
            name: "Báo cáo",
            icon: <AreaChartOutlined style={default_font_style} />,
            child: []
        },
        {
            path: "/setting",
            name: "Cài đặt",
            icon: <SettingOutlined style={default_font_style} />,
            child: []
        }


    ]

    const history = useHistory();

    const [theme, setTheme] = useState('dark');

    const [current, setCurrent] = useState('1');

    const changeTheme = () => {
        if (theme == 'light')
            setTheme('dark');
        else
            setTheme('light');
    }

    const handleClick = (e: any) => {
        console.log('click ', e);

        setCurrent(e.key);
    }



    const handleRouteClick = (path: string) => {
        history.push(path);
    }
    return (
        <Menu
            id="left-menu"
            inlineCollapsed={true}
            theme='dark'
            onClick={handleClick}
            style={{ width: '60px', height: '100%', background: '#1D243E' }}
            defaultOpenKeys={['default-menu-item-0']}
            selectedKeys={[current]}
            mode="inline"
        >
            {routers.map((route, index) => {
                if (route.path == "")
                    return (
                        <div key={`default-menu-item-${index}`} style={{ height: '50px'}}>
                            <img src={logo} style={{ height: '32px', width: '32px', marginLeft: '14px', marginTop: '20px', cursor: 'pointer' }} alt="Onsales CRM" />
                        </div>
                    )
                else if (route.child.length == 0)
                    return (
                        <Menu.Item key={`default-menu-item-${index}`} onClick={event => history.push(route.path)} icon={route.icon} style={{margin : '20px 0px', padding: '0px 20px'  }}>
                            {route.name}
                        </Menu.Item>
                    )
                else {
                    return (
                        <SubMenu key={`default-sub-menu-item-${index}`} icon={route.icon} title={route.name} style={{margin : '20px 0px', padding: '0px' }} >
                            {route.child.map((children: any, children_index: any) => {
                                return (
                                    <Menu.Item key={`children-menu-${children_index}`} onClick={event => history.push(children.path)}>
                                        {children.name}
                                    </Menu.Item>
                                )
                            })}
                        </SubMenu>
                    )
                }
            }
            )}
        </Menu>
    );
}
