import { IAccountModel } from 'store/index';
import axios, { AxiosInstance } from 'axios';
import aes from 'components/aes';

const base_url = process.env.REACT_APP_BASE_URL;

export const BASE_URL = base_url;

const http_instance = axios.create({
    baseURL: `http://localhost:6688/`,
    // baseURL: `${base_url}/`
});

let init = function (...inst: AxiosInstance[]) {

    try {
        const _user = localStorage.getItem("auth");

        const user: IAccountModel = JSON.parse(aes.d(_user!));//JSON.parse(_user === null ? "{}" : _user);

        if (user != undefined && user != undefined && user.token != undefined && user.token != "") {
            inst.forEach(ins => {
                ins.defaults.headers.common['Authorization'] = 'Bearer ' + user.token;
            });
        }
    } catch (ex) {
        console.log(ex)
    }

    
}

init(http_instance);

export default {
    crm: http_instance
}

export const ahal = (user: IAccountModel, ...instance: AxiosInstance[]) => {
    if (user != undefined && user.token != undefined) {
        instance.forEach(ins => {
            ins.defaults.headers.common['Authorization'] = 'Bearer ' + user.token;
        })
    }
}


export const rhal = (...instance: AxiosInstance[]) => {
    instance.forEach(ins => {
        delete ins.defaults.headers.common["Authorization"];
    })
}