import React, { useEffect, useState } from 'react';
import FormManager, { IField } from 'components/form';

import { fabric } from 'fabric';
import { Button, Space, Tabs } from 'antd';
import { Component2ColumnLayout, TwoLayoutPropsModel } from 'layouts/ComponentDefaultLayout';

import transparencyIMG from 'assets/transparency.png';
import { FormInstance } from 'antd/lib/form';
import { BlockOutlined, BorderOutlined, FontColorsOutlined } from '@ant-design/icons';
import { stringify } from 'uuid';
import { type } from 'os';
import { useDispatch, useSelector } from 'react-redux';
import { getDetail, setObjectDetail } from 'store/modules/object.store';

import { makeAutoObservable } from "mobx"
import { observer } from "mobx-react"

const { TabPane } = Tabs;

const canvasRef = React.createRef<any>();

const tabRef = React.createRef<any>();

interface IStateModel {
    canvas: undefined | fabric.Canvas, object: Object
}

export default () => {

    let current: any = undefined;

    let currents: any[] = [];

    const dispatch = useDispatch();

    const objectDetail = useSelector(getDetail);

    //const [canvas, setCanvas] = useState<undefined | fabric.Canvas>(undefined);

    //const [object, setObject] = useState<Object | undefined>(undefined);

    let object = {};

    //const [isLoading, setIsLoading] = useState<boolean>(false);

    let canvas: fabric.Canvas;

    useEffect(() => {

        canvas = new fabric.Canvas(canvasRef.current);


        if (canvas != undefined) {

            canvasContainerInit(200, 300);

            canvas.renderAll();
        }

    }, []);

    useEffect(() => {
        alert('change' + JSON.stringify(objectDetail));
    }, [objectDetail]);


    const canvasContainerInit = (width: number, height: number, background?: string) => {

        //let _canvas = canvas;

        canvas.setWidth(width);
        canvas.setHeight(height);
        canvas.setBackgroundColor(background ?? '#fff', () => { });

        canvas.renderAll();
        //setCanvas(_canvas);
    }


    const AddRect = () => {

        console.log(canvas);

        var rect = new fabric.Rect({
            top: 100,
            left: 100,
            width: 60,
            height: 70,
            fill: 'red'
        });

        rect.on('mousedown', (e) => {
            console.log(e.target);



        });

        currents.push({ name: 'layer', context: rect });

        // rect.on({
        //     'object:moving': updateControls,
        //     'object:scaling': updateControls,
        //     'object:resizing': updateControls,
        //     'object:rotating': updateControls,
        //     'object:skewing': updateControls
        //   });

        canvas!.add(rect);

    }

    const changeTab = () => {

    }

    const AddText = () => {

        console.log(canvas);
        var text = new fabric.Text("abc", {
            fontSize: 20
        });

        // text.on

        canvas!.add(text);
    }

    const config_form = React.createRef<FormInstance>();

    const fields_config: IField[] = [
        {
            type: 'heading',
            label: 'Config',
            name: 'label1'
        },
        {
            type: 'input',
            label: 'width',
            name: 'width',
            required: true,
            disabled: false
        },
        {
            type: 'input',
            label: 'height',
            name: 'height',
            required: true,
            disabled: false
        },
    ]

    const changeCanvasSetting = (values: any) => {
        config_form.current?.validateFields()
            .then((values: any) => {
                console.log(values);
                canvasContainerInit(values.width, values.height);
                //form.current!.resetFields();
            }, () => {
                console.log('please fill or complete form')
            }).finally(() => {

            })
    }

    const changeObjectSetting = () => {

        object = {a : '1'}

        //dispatch(setObjectDetail({ a: '1' }))
    }

    return (
        <>
            <h1>IM</h1>
            <Space direction='horizontal'>
                <Button onClick={AddRect} icon={<BlockOutlined />}>Add Rect</Button>
                <Button onClick={AddText} icon={<FontColorsOutlined />}>Add Text</Button>
            </Space>

            <Component2ColumnLayout
                fixed='left'
                minus={400}
                left={
                    <React.Fragment>

                        <Tabs defaultActiveKey="1">
                            <TabPane tab="Canvas Config" key="1">
                                <FormManager fields={fields_config} default={{ width: 200, height: 300 }} form={config_form} layout='horizontal' />
                                <Button onClick={changeCanvasSetting} type="primary">
                                    Change
                                </Button>
                            </TabPane>
                            <TabPane tab="Information" key="2">
                                <ObjectDetailMobx object={object} onSubmit={changeObjectSetting} />
                            </TabPane>
                            <TabPane tab="Helper" key="3">

                                {JSON.stringify(currents)}
                            {currents.map((x : any) => 
                                <>
                                    {x.name}
                                </>
                            )}
                            </TabPane>
                        </Tabs>
                    </React.Fragment>
                }
                right={
                    <>
                        <div style={{ border: '1px dashed #ccc', padding: '10px', margin: '0px 10px', overflow: 'auto' }}>
                            <canvas ref={canvasRef} id="canvas" width="300" height="300" style={{ border: '1px solid #ccc' }}>

                            </canvas>

                        </div>

                    </>
                }
            />


        </>
    )
}


const ObjectDetailMobx = observer((props : { object : Object, onSubmit : Function }) => 
{
    return (
        <>
            {JSON.stringify(props.object)}
            <Button onClick={() => { props.onSubmit() }} >change</Button>
        </>
    )
});
// (
//     <button onClick={() => timer.reset()}>Seconds passed: {timer.secondsPassed}</button>
// ))

const ObjectDetail = (props: { object: any, onSubmit: Function }) => {
    return (
        <>
            {JSON.stringify(props.object)}
            <Button onClick={() => { props.onSubmit() }} >change</Button>
        </>
    )
}

