import { createSlice } from '@reduxjs/toolkit';

import { IAccountModel, IStateModel } from 'store/index';

import http , {ahal,rhal} from 'utils/http.client'

import accountSerivce from 'services/account';

import aes from 'components/aes';

import Cookies from 'js-cookie';

// const auth = Cookies.get('auth');
const auth = window.localStorage.getItem('auth');

interface model {
  username: string, id: string, token: string, permission :any, fullname : string, organizationId : number
}

const df = {
  token: "",
  name: "",
  id: "",
  isAuthorized: false,
  permission: [],
  organizationId : 0
}

const current = (function () {

  if (auth !== undefined) {
    try {
      const current: model = JSON.parse(aes.d(auth!));

      return {
        token: current.token,
        name: current.username,
        id: current.id,
        fullname : current.fullname,
        isAuthorized: true,
        permission: current.permission,
        organizationId : current.organizationId
      };
    } catch (e) {
      //console.log(e);
    }
  }
  return df
}());

// const psm = (function(){
//   setTimeout( async () => {
//     return "a";
//   }, 2000);
// })();

// console.log(psm);

export const accountStore = createSlice({
  name: 'account',
  initialState: {
    ...current
  },
  reducers: {
    setAuthorized: state => {
      state.isAuthorized = true;
    },
    removeAuthorized: state => {
      // debugger;
      // state.token = "";
      // state.name = "";
      // state.id = "";
      state.isAuthorized = false;

      rhal(http.crm);

    },
    setIdentity: (state: any, action: {type : string, payload : any}) => {
      
      if(action == undefined || action.payload == undefined){
        console.log('Hit Set Identity')
        return;
      }
        

      state.token = action.payload.token;
      state.name = action.payload.name;
      state.id = action.payload.id;
      state.isAuthorized = true;
      state.fullname = action.payload.fullname;
      state.permission = action.payload.permission;
      state.organizationId = action.payload.organizationId;

      ahal(action.payload, http.crm);
    },
    setPermission : (state : any, action : {type : string, payload : {module : string, action : string , isActive : boolean} }) => {
      if(state.permission.filter((x : any) => x.module == action.payload.module && x.action == action.payload.action ).length == 0){
        state.permission.push(action.payload);
      }
      //state.permission = [action.payload];
    }
  },
});

export const { setAuthorized, removeAuthorized, setIdentity, setPermission } = accountStore.actions;

export const setIdentityAsync = (data: IAccountModel) => (dispatch: (arg0: { payload: any; type: string; }) => void) => {
  setTimeout(() => {
    dispatch(setIdentity(data));
  }, 1000);
};



